import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IBleState } from '../state/IBleState';
import { AppThunk } from '../store';
import { GhostBle } from '../../ble/GhostBle';

const initialState: IBleState = {
  connecting: false,
  connected: false,
  updating: false,
  connectAttempts: 0,
  authKey: localStorage.getItem('ghostmv1.auth') || '',
  battery: 0,
  profile: 0,
  temperatures: [0, 0, 0, 0, 0, 0],
};

const disconnect = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.disconnect();
  dispatch(disconnected());
};

const connect = (): AppThunk => async (dispatch, getState) => {
  try {
    await GhostBle.connect();
  } catch (e) {
    //dispatch(disconnected());
    return;
  }
  dispatch(connectSuccess());
};

const pair = (): AppThunk => async (dispatch, getState) => {
  try {
    await GhostBle.pair();
  } catch (e) {
    dispatch(connectFailed("An error occured establishing a connection to Ghost MV1 BLE device."));
    return;
  }
  dispatch(connectSuccess());
};

const updateCurrentProfile = (profile: number): AppThunk => async (dispatch, getState) => {
  try {
    await pauseNotifications();
    dispatch(startUpdate());
    await GhostBle.updateCurrentProfile(profile);
  } catch (e) {
    return;
  }
  dispatch(setCurrentProfileForce(profile));
  await startNotifications();
  setTimeout(() => {
    dispatch(completeUpdate());
  }, 500);
}

const updateTemperature = (values: number[], profile: number): AppThunk => async (dispatch, getState) => {
  try {
    await pauseNotifications();
    dispatch(startUpdate());
    await GhostBle.updateTemperature(values, profile);
  } catch (e) {
    return;
  }
  dispatch(setTemperatures(values));
  await startNotifications();
  setTimeout(() => {
    dispatch(completeUpdate());
  }, 500);
}

const startNotifications = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.startNotifications();
}

const pauseNotifications = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.pauseNotifications();
}

const bleSlice = createSlice({
  name: 'BLE',
  initialState,
  reducers: {
    startUpdate:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: true,
    }),
    completeUpdate:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: false,
    }),
    connectStart: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: true,
      connected: false,
      error: undefined,
      connectAttempts: 0,
    }),
    connectSuccess: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: false,
      connected: true,
      error: undefined,
      connectAttempts: 0,
    }),
    connectFailed: (state: IBleState, action: PayloadAction<string>) => ({
      ...state,
      connecting: false,
      connected: false,
      error: action.payload,
      connectAttempts: state.connectAttempts + 1,
    }),
    disconnected: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: false,
      connected: false,
      error: undefined,
      connectAttempts: 0,
    }),
    setAuthKey: (state: IBleState, action: PayloadAction<string>) => ({
      ...state,
      authKey: action.payload
    }),
    setBatteryLevel: (state: IBleState, action: PayloadAction<number>) => ({
      ...state,
      battery: action.payload
    }),
    setCurrentProfileForce: (state: IBleState, action: PayloadAction<number>) => {
      return {
        ...state,
        profile: action.payload,
      };
    },
    setCurrentProfile: (state: IBleState, action: PayloadAction<number>) => {
      if (state.updating) {
        return state;
      } else {
        return {
          ...state,
          profile: action.payload,
        };
      }
    },
    setTemperatures: (state: IBleState, action: PayloadAction<number[]>) => ({
      ...state,
      temperatures: action.payload
    }),
    clearError: (state: IBleState, action: PayloadAction) => ({
      ...state,
      error: undefined,
    }),
  }
});

export { 
  connect, disconnect, pair, 
  updateCurrentProfile, updateTemperature, 
  startNotifications, pauseNotifications, 
};

export const { 
  connectStart, connectSuccess, connectFailed, 
  disconnected, 
  setAuthKey, 
  setBatteryLevel, 
  setCurrentProfile,
  setCurrentProfileForce,
  setTemperatures,
  clearError,
  startUpdate,
  completeUpdate,
} = bleSlice.actions;
export default bleSlice.reducer